body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
}

.header {
  width: 100%;
  height: 15%;
  background-color: antiquewhite;

  display: flex;
  align-items: center;
  justify-content: space-between;
}
.right-section-header {
  width: 50%;
  height: 100%;
}
.right-section-header {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.login-btn {
  width: 10%;
  height: 10%;
}
.signup-btn {
  width: 10%;
  height: 10%;
}
.search-btn {
  width: 10%;
  height: 10%;
}
.body {
  height: 80%;
  width: 100%;

  display: flex;
  justify-content: space-around;
}
.contents {
  height: 100%;
  width: 100%;
  bottom: 1px solid red;

  display: flex;
  align-items: center;
  flex-direction: column;
}
.post {
  height: 400px;
  width: 100%;
  border: 1px solid black;
  border-radius: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.post-header {
  height: 20%;
  width: 100%;
  word-break: break-all;
  text-align: center;
  font-size: 32px;
}
.post-body {
  height: 80%;
  width: 100%;
  text-align: center;
  word-break: break-word;
  font-size: 26px;
}
.post-footer {
  height: 20%;
  width: 100%;
  word-break: break-all;
  text-align: center;
}
.side-bar {
  height: 100%;
  width: 30%;
  font-size: 22px;

  display: flex;
  flex-direction: column;
}

.createNewPost {
  width: 100%;
  height: 100%;
}

.newPost {
  width: 75%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
}
.new-post-header {
  width: 100%;
  height: 100%;
  background-color: blanchedalmond;

  display: flex;
}

.new-post-title {
  width: 100%;
  height: 70px;
  border: 1px solid red;
  border-radius: 15px;
  font-size: 30px;
  outline: none;
}

.new-post-body {
  width: 100%;
  height: 600px;
  border-radius: 15px;
  margin-top: 10px;
  border: 1px solid red;
  font-size: 30px;
  outline: none;
}

.new-post-footer {
  width: 100%;
  height: 70px;
  border-radius: 15px;
  margin-top: 10px;
  border: 1px solid red;
  font-size: 30px;
  outline: none;
}
.submit-btn {
  width: 10%;
  height: 100%;
  border-radius: 10px;
}

